export default {
  name: "itemSingle",
  props: {
    item: { type: Object, required: true }
  },
  data () {
    return {
      isVisited: false,
    }
  },
  mounted () {
      if (localStorage.visited) {
          this.isVisited = JSON.parse(localStorage.visited).includes(this.item.date)
      } 
  }
}
